		sans-serif;

	font-family: var(--font-family);

	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

:root {
	--font-family:
		-apple-system,
		blinkmacsystemfont,
		'Montserrat',
