.loading {
	position: fixed;
	z-index: 9999;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	pointer-events: none;
}

.loading-text {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;

	width: 100%;
	height: 100px;
	margin: auto;

	text-align: center;

	line-height: 100px;
}

.loading-text span {
	display: inline-block;

	margin: 0 5px;

	color: #FFF;

	font-family: "Quattrocento Sans", sans-serif;
}

